#lnb {
    min-width: 30rem;
    padding-top: 12rem;
    position: sticky;
    top: 0;
    align-self: flex-start;
}
#lnb::after {
    content: "";
    border-right: 1px solid rgb(233, 233, 233);
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    min-height: 100vh;
}
#lnb .depth2-wrap .depth2-head .depth2-subject {
    display: block;
    padding: 0.8rem 3rem;
    color: rgb(17, 17, 17);
    font-weight: 700;
    font-size: 2rem;
}
