    margin: 0 1.5rem;
}
#lnb .depth2-wrap .depth2-body .depth2-list .depth2-item .depth2 {
    display: block;
    padding: 1rem 1.5rem;
    border-radius: 0.2rem;
    color: rgb(136, 136, 136);
    font-weight: 400;
    font-size: 1.8rem;
}
#lnb .depth2-wrap .depth2-body .depth2-list .depth2-item.active .depth2:not(.dropdown) {
    background: rgb(246, 248, 252);
    color: rgb(79, 111, 203);
    font-weight: 500;
    font-size: 1.8rem;
}
