    color: rgb(17, 17, 17);
    font-weight: 500;
    font-size: 1.8rem;
}
#lnb .depth3-wrap {
    position: relative;
    margin-left: 2.8rem;
}
#lnb .depth3-wrap::before {
    content: "";
    border-left: 1px solid rgb(233, 233, 233);
    height: calc(100% - 3.6rem);
    position: absolute;
    left: -0.6rem;
    top: 50%;
    transform: translateY(-50%);
}
#lnb .depth3-wrap .depth3-item .depth3 {
    display: block;
    position: relative;
    padding: 0.6rem 1rem;
    border-radius: 0.2rem;
    color: rgb(136, 136, 136);
    font-weight: 400;
    font-size: 1.6rem;
