#lnb .depth3-wrap .depth3-item.active .depth3 {
    display: flex;
    align-items: center;
    background-color: rgb(246, 248, 252);
    color: rgb(79, 111, 203);
    font-weight: 500;
    font-size: 1.6rem;
}
#lnb .depth3-wrap .depth3-item.active .depth3:after {
    content: "";
    display: inline-block;
    width: 1.6rem;
    height: 1.6rem;
    background-image: url("data:image/svg+xml,%0A%3Csvg width='7' height='11' viewBox='0 0 7 11' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M4.43842 5.49908L0.96875 2.02941L2.02941 0.96875L6.55974 5.49908L2.02941 10.0294L0.96875 8.96875L4.43842 5.49908Z' fill='rgb(79,111,203)'/%3E%3C/svg%3E%0A");
    background-repeat: no-repeat;
    background-size: 0.7rem 1.1rem;
    background-position: center;
}
#lnb .depth3-wrap .depth3-item.active .depth3:before {
    content: "";
    position: absolute;
    left: -0.75rem;
    width: 0.4rem;
    height: 0.4rem;
    border-radius: 100%;
    background-color: rgb(79, 111, 203);
}

