body {
    /* Accent */
}
body .blind {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    border: 0;
    white-space: nowrap;
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
    overflow: hidden;
}
body .accent.color-01 {
    color: rgb(79, 111, 203) !important;
}
body .skip ul li a {
    display: flex;
    position: absolute;
    top: -10rem;
    right: 0;
    left: 0;
    height: 5.6rem;
    background: rgb(17, 17, 17);
    color: rgb(255, 255, 255);
    font-size: 16px;
    line-height: 5.6rem;
    text-align: center;
    flex-direction: column;
    justify-content: center;
    z-index: 99999;
}
body .skip ul li a:focus-within, body .skip ul li a:focus, body .skip ul li a:active {
    top: 0;
}

* {
    padding: 0;
    margin: 0;
    border: 0;
    background: transparent;
    color: inherit;
    line-height: 150%;
    font-style: normal;
    box-sizing: border-box;
    word-wrap: break-word;
    word-break: keep-all;
    touch-action: auto;
    -webkit-tap-highlight-color: transparent !important;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0) !important;
}

*:before,
*:after {
    box-sizing: inherit;
}

